@charset "utf-8";
html {
  width: 100%;
  height: 100%;
}
body {
  font-size: .259259rem;
}
input {
  padding-left: 10px;
  outline: none;
  font-size: .259259rem;
  font-weight: normal;
  color: #666666;
}
input::placeholder {
  color: #999999;
}
.f-btn {
  border: 0.018519rem solid #D6E1E5;
  padding: .092593rem .148148rem;
  border-radius: .074074rem;
}
.f-btn.f-btn-primary {
  background: #3196FA;
  text-align: center;
  color: #ffffff;
}
